import navBar from '@/components/nav-bar.vue'
import {
	bindzfb,
	sendcode
} from '@/api/user.js'
export default {
	data: () => ({
		yzm: '获取验证码',
		name: '',
		zfbname: '',
		code: '',
		isSend: false,
		phone: '',
		user:null
	}),
	components: {
		navBar
	},
	onLoad() {
		this.getUser();
	},
	methods: {
		getUser(){
			try{
				this.user = uni.getStorageSync('user');
				this.phone = this.user.phone
			}catch(e){
				//TODO handle the exception
			}
		},
		back() {
			uni.switchTab({
				url:"/pages/index/user"
			})
		},
		sendyzm() {
			if (!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.zfbname) || !this.zfbname) {
				uni.showToast({
					title: '请输入正确的手机格式,',
					icon: "none"
				})
				return
			}
			if (!this.isSend) {
				sendcode(this.zfbname, 'login').then(res => {
					if (res.code == 200) {
						uni.showToast({
							title: '已发送'
						})
						this.isSend = true
						let timer = null;
						let cut = 60;
						timer = setInterval(() => {
							this.yzm = --cut + "S"
							if (cut == 0) {
								clearInterval(timer)
								this.yzm = '发送验证码'
								this.isSend = false
							}
						}, 1000)
					}
				})
			}
		},
		submit() {
			if (!this.name || !this.zfbname || !this.code) {
				uni.showToast({
					title: "姓名手机号或验证码不能为空",
					icon: "none"
				})
				return
			}
			if (!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.zfbname)) {
				uni.showToast({
					title: '请输入正确的手机格式,',
					icon: "none"
				})
				return
			}
			console.log('zfb',this.zfbname, this.name, this.code,this.phone)
			bindzfb(this.zfbname, this.name, this.code,this.phone).then(res => {
				 
				if (res.code != 200) {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					})
					return
				}
				this.user.zfbname = this.zfbname
				uni.setStorageSync('user',this.user);
				uni.showToast({
					title: "绑定成功",
				})
				uni.navigateTo({
					url: '/pages/index/cash-withdrawal'
				})
			})
		}
	}
}
